/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --font-body: "Lato", sans-serif;
  --font-display: "League Spartan", sans-serif;
}

:root {
  --nwcGreen: hsl(167, 100%, 20.2%);
  --nwcLightGreen: hsl(120, 55.5%, 44.9%);
  --nwcRed: hsl(355, 84.5%, 54.5%);
  --nwcYellow: hsl(47, 100%, 46.1%);
  --nwcBlue: hsl(220, 100%, 23.1%);
  --nwcLightBlue: hsl(207, 100%, 40.6%);
  --green: hsl(166, 100%, 18.4%);
  --lightgreen: hsl(247, 9.8%, 36.1%);
  --fadeRed: hsl(360, 82.5%, 57.5%);
  --brightRed: hsl(358, 72.6%, 45.9%);
  --red: hsl(358, 84.6%, 51.8%);
  --darkBlue: hsl(235, 52.2%, 9%);
  --gold: hsl(54, 69.2%, 84.7%);
  --lightGold: hsl(54, 71.4%, 97.3%);
  --black: hsl(0, 0%, 0%);
  --richBlack: hsl(0, 0%, 6%);
  --lightGrey: hsl(210, 1%, 60.8%);
  --grey: hsl(0, 0%, 50.2%);
  --white: hsl(0, 0%, 100%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  font-weight: 500;
  color: var(--black);
  line-height: 1.3;
}
body.noscroll {
  overflow: hidden;
}

.container {
  width: 80%;
  max-width: 75rem;
  margin: 0 auto;
}
.row {
  padding: 0 1.25rem;
}

h1,
h2,
h3,
h4 {
  margin-top: 0;
  font-family: var(--font-display);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.btn__contact a, .btn__login a {
  font-family: var(--font-display);
  font-size: 1.125rem;
  font-weight: 700;
  padding: 0.625rem 1.25rem;
  color: var(--nwcGreen);
  background-color: var(--white);
  border: 0.0625rem solid var(--white);
  border-radius: 0.3125rem;
  margin-left: 1.25rem;
  cursor: pointer;
}
@media (max-width: 59.999375em) {
  .btn__contact a, .btn__login a {
    margin-left: 0;
  }
}
.btn__contact a:hover, .btn__login a:hover {
  background-color: var(--nwcLightGreen);
  border: 0.0625rem solid var(--nwcLightGreen);
  color: var(--white);
  transition: color 250ms ease-in-out;
}

.has-fade {
  visibility: hidden;
}

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 200ms ease-in-out forwards;
}

@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  99% {
    visibility: hidden;
    opacity: 0;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
.fade-out {
  animation: fade-out 200ms ease-in-out forwards;
}

.header {
  background-color: var(--nwcGreen);
  display: flex;
  flex-direction: row;
  align-items: center;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 998;
}
.header.open .overlay {
  display: block;
}
.header.open .header__toggle > span:first-child {
  transform: rotate(45deg);
}
.header.open .header__toggle > span:nth-child(2) {
  opacity: 0;
}
.header.open .header__toggle > span:last-child {
  transform: rotate(-45deg);
}
.header .overlay {
  opacity: 0;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(var(--nwcLightGreen), transparent);
}
.header__logo img {
  width: 10.9375rem;
  height: 5.3125rem;
}
.header__section {
  width: 100%;
}
.header__toggle > span {
  display: block;
  width: 1.625rem;
  height: 0.125rem;
  background-color: var(--white);
  transition: all 300ms ease-in-out;
  transform-origin: 0.1875rem 0.0625rem;
}
.header__toggle > span:not(:last-child) {
  margin-bottom: 0.3125rem;
}
.header__nav {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 0.625rem 0 0;
}
.header__nav--open {
  position: absolute;
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: var(--nwcGreen);
  width: 100%;
  left: 50%;
  transform: translate(-50%);
  height: auto;
  top: 125px;
  padding: 20px;
  z-index: 990;
  margin: 0 auto;
  transition: all 300ms ease-in-out;
}
@media (max-width: 59.999375em) {
  .header__nav--open {
    top: 6.875rem;
  }
}
.header__media {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 0.625rem;
}
@media (max-width: 59.999375em) {
  .header__media {
    flex-direction: column;
  }
}
.header__search {
  margin-left: 1.875rem;
}
@media (max-width: 59.999375em) {
  .header__search {
    width: 100%;
    margin: 1.25rem 0 0;
  }
}
.header__search input[type=text] {
  font-size: 1rem;
  color: var(--lightGrey);
}
.header__search--placeholder {
  padding: 0.3125rem;
  border-radius: 0.3125rem;
  border: 0.0625rem solid var(--newGreen);
}
@media (max-width: 59.999375em) {
  .header__search--placeholder {
    width: 100%;
  }
}
@media (max-width: 59.999375em) {
  .header__social {
    width: 30%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__social a {
  margin-left: 1.25rem;
}
.header__social a:first-child {
  margin-left: 0;
}
.header__menu {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
}
@media (max-width: 59.999375em) {
  .header__menu {
    flex-direction: column;
  }
}
.header__menu ul {
  display: flex;
}
.header__menu li {
  font-family: var(--font-display);
  font-size: 1.0625rem;
  font-weight: 700;
  list-style: none;
  margin-left: 1rem;
}
.header__menu li:first-child {
  margin-left: 0;
}
@media (max-width: 59.999375em) {
  .header__menu li {
    margin: 0.625rem 0;
  }
}

@media (max-width: 59.999375em) {
  .head {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
}

.nav {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  background-color: var(--nwcGreen);
  width: 100%;
}
@media (max-width: 59.999375em) {
  .nav {
    padding: 1.25rem;
  }
}
.nav__list {
  padding: 0;
}
.nav__list--open {
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding-left: 0;
  transition: all 300ms ease-in-out;
}
@media (max-width: 59.999375em) {
  .nav__list, .nav__item {
    flex-direction: column;
  }
}
.nav__list a, .nav__item a {
  color: var(--white);
  cursor: pointer;
}
.nav__list a:hover, .nav__item a:hover {
  color: var(--gold);
  transition: all 200ms ease-in-out;
}

.has-submenu {
  display: flex;
  flex-direction: column;
  position: relative;
  padding-bottom: 1.25rem;
}
.has-submenu ul.submenu {
  display: none;
}
.has-submenu:hover ul.submenu {
  display: flex;
  flex-direction: column;
}
.has-submenu:hover ul.submenu li.subitem {
  padding-left: 0;
  margin: 0.625rem 1.875rem;
  width: 100%;
}

.submenu {
  justify-content: center;
  align-items: center;
  padding-left: 0;
  text-align: center;
  position: absolute;
  background-color: var(--nwcGreen);
  padding: 0 1.25em 1.25rem;
  left: -3.75em;
  min-width: 12.5rem;
  top: 1.25em;
}
.submenu li {
  margin-left: 0;
}

.subitem {
  margin: 0.625em 0 0 0;
  color: var(--white);
}

@media (max-width: 59.999375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 60em) {
  .hide-for-desktop {
    display: none;
  }
}

.hero__index {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  left: 50%;
  transform: translateX(-50%);
  margin: 0;
  overflow: hidden;
  padding: 0;
  margin-bottom: 1.25rem;
}
@media (max-width: 59.999375em) {
  .hero__index {
    transition: all 350ms ease-in-out;
  }
}
.panels {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 25rem;
  overflow: hidden;
  position: relative;
  justify-content: center;
  align-items: center;
}
@media (max-width: 59.999375em) {
  .panels {
    flex-direction: column;
  }
}
.panels__halfWidth {
  margin-bottom: 1.25rem;
  height: auto;
}
.panels__halfWidth--conf h2, .panels__halfWidth--pharm h2 {
  font-family: var(--font-display);
  font-size: 3rem;
  color: var(--white);
}
.panels__halfWidth--conf, .panels__halfWidth--pharm {
  display: flex;
  align-items: center;
  justify-content: center;
  background-repeat: no-repeat;
  background-position: center top;
  width: 50%;
  height: 25rem;
}
@media (max-width: 59.999375em) {
  .panels__halfWidth--conf, .panels__halfWidth--pharm {
    width: 100%;
  }
}
.panels__halfWidth--conf {
  background-image: url("../img/panels_conference_1300x400-min.jpg");
}
.panels__halfWidth--pharm {
  background-image: url("../img/panels_pharmacy_1300x400-min.jpg");
}
@media (max-width: 59.999375em) {
  .panels__halfWidth--pharm {
    margin-top: 1.25rem;
  }
}
.panels__fullWidth {
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("../img/panels_tradeshow_1920x400-min.jpg");
  background-repeat: no-repeat;
  background-position: center top;
  width: cover;
  margin-bottom: 1.25rem;
  height: 25rem;
}
.panels__fullWidth--trade h2 {
  font-family: var(--font-display);
  font-size: 3rem;
  color: var(--white);
}

.card {
  padding: 1.25rem;
}
.card__button {
  width: 1rem;
  height: 1rem;
  border-radius: 0.9375rem;
  background-color: var(--nwcLightGreen);
}

.footer {
  background-color: var(--nwcGreen);
  display: flex;
  padding-top: 2.8125rem;
}
.footer__nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}
.footer__nav--menu {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}
.footer__logo img {
  width: 10.9375rem;
  height: 5.3125rem;
}